<template>
    <custom-section sec-id="dataset"
                    sec-title="Dataset"
                    :sec-wide="false"
                    :sec-divider="true">
      <!-- Dataset description -->
      <p class="section-body" >
        The dataset used in this project is gathered from a structured marketing website. This dataset contains
        an overall number of 106,688 images and 15 categories of vehicle models which is divided into the
        following subsets :
      </p>
      <!-- Dataset subsets -->
      <ul class="list-group fa-ul">
        <li v-for="(item,index) in dataset" :key="index" class=" pt-3 pb-3  flex-column align-items-start">
          <div class="d-flex w-100 justify-content-between pb-2">
            <h6 class="mb-1 font-weight-bold">
              <span class="fa-li">
                <i class="fas fa-check-square"></i>
              </span>
              {{item.name}}
            </h6>
            <!-- View samples -->
            <small v-if="item.samplesUrl">
              <a class="text-dark" :href="item.samplesUrl" target="_blank">
              View Samples</a>
            </small>
          </div>
          <p class="mb-1">{{item.desc}}</p>
          <!-- Download subset -->
          <small>
            <a class="text-dark" :href="item.downloadUrl" target="_blank">
              Download this subset
            </a>
          </small>
        </li>
      </ul>

      <!-- Download button -->
      <a href="https://mega.nz/#F!1HxDCDxb!cgLisUq7nuf4t_7QhUHs-g"
         target="_blank" class="btn btn-outline-dark mt-3">
        <em class="fas fa-download btn-icon"></em>
        Download dataset
      </a>
    </custom-section>
</template>

<script>
import CustomSection from '../components/elements/CustomSection'
export default {
  name: 'Dataset',
  components: {CustomSection},
  data: function () {
    return {
      dataset: [
        {
          name: 'Noisy subset',
          desc: 'Includes 45,000 images randomly sub-sampled from the main dataset (5,000 images per class)',
          samplesUrl: '/static/media/samples/noisy-subset.jpg',
          downloadUrl: 'https://mega.nz/#F!1HxDCDxb!cgLisUq7nuf4t_7QhUHs-g?QTJwTCDS'
        },
        {
          name: 'Clean subset',
          desc: 'Contains 6,300 carefully annotated images (700 images per class). The annotation task is performed on 46,820 images, and there are no overlaps between the clean subset and the noisy one',
          samplesUrl: '/static/media/samples/clean-subset.jpg',
          downloadUrl: 'https://mega.nz/#F!1HxDCDxb!cgLisUq7nuf4t_7QhUHs-g?QWBGCCZB'
        },
        {
          name: 'Benchmark subset',
          desc: 'Includes 4,950 images of clean images and labeled noisy ones (450 clean images per class and 900 noisy images in total)',
          downloadUrl: 'https://mega.nz/#F!1HxDCDxb!cgLisUq7nuf4t_7QhUHs-g?5SwUDJZS'
        }

      ]
    }
  }
}
</script>

<style scoped>
  .section-body {
    text-align: justify;
    line-height: 1.6;
  }
</style>



