<template>
  <div class="jumbotron jumbotron-fluid">
    <div class="container">
      <h1 v-if="jumboTitle" class="title">{{jumboTitle}}</h1>
      <p v-if="jumboSubtitle" class="lead">{{jumboSubtitle}}</p>
    </div>
  </div>

</template>

<script>
export default {
  name: 'TheJumbotron',
  data: function () {
    return {
      jumboTitle: 'VEHICLE RECOGNITION',
      jumboSubtitle: 'Transfer Learning Approach for Classification and Noise Reduction on Noisy Web Data'
    }
  }
}
</script>

<style scoped>

  .jumbotron {

    border-radius: 0;
    background: linear-gradient(
      rgba(0, 0, 0, 0.3),
      rgba(0, 0, 0, 5)
    ),
    url('/static/media/cover.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    color: white !important;
    height: 22rem;

    font-family: "Helvetica", "Arial", sans-serif;
    margin-top:0;
    margin-bottom: 0;

  }

  .jumbotron .title
  {

    margin-top:5rem;
    text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
  }

  .jumbotron .lead {

    font-weight: 300;
    margin-top: -0.5rem;
    text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.9);

  }
</style>



