// Default constants
const DEFAULT_CANVAS_SIZE = 320
const DEFAULT_FRAME_MARGIN = 10
const DEFAULT_FRAME_LENGTH = 10
const DEFAULT_FRAME_WIDTH = 3
const DEFAULT_DARK_COLOR = '#00000055'
const DEFAULT_WHITE_COLOR = '#FFFFFF'
const DEFAULT_DRAW_MARGIN = 10
const DEFAULT_SHADOW_OFFSET = 2

// Preprocess the image
export function procImg (srcCanvas, dstCanvas, dstCanvasEx, imgSize) {
  // Determine contexts
  const dstCtx = dstCanvas.getContext('2d')
  const dstCtxEx = dstCanvasEx.getContext('2d')

  // Define parameters
  const srcWidth = srcCanvas.width
  const srcHeight = srcCanvas.height
  const cvSize = DEFAULT_CANVAS_SIZE
  let dstWidth
  let dstHeight
  let frameSize

  // Detect the dominant orientation
  if (srcWidth > srcHeight) {
    dstWidth = cvSize
    dstHeight = cvSize * (srcHeight / srcWidth)
    frameSize = dstHeight
  } else {
    dstWidth = cvSize * (srcWidth / srcHeight)
    dstHeight = cvSize
    frameSize = dstWidth
  }

  // Set canvas width and height
  dstCanvas.width = dstWidth + DEFAULT_DRAW_MARGIN * 2
  dstCanvas.height = dstHeight + DEFAULT_DRAW_MARGIN * 2

  // Determine Region of Interest
  const frameMargin = DEFAULT_FRAME_MARGIN
  const frameLength = DEFAULT_FRAME_LENGTH
  const roiRect = {
    x: DEFAULT_DRAW_MARGIN + (dstWidth / 2.0) - frameSize / 2,
    y: DEFAULT_DRAW_MARGIN + (dstHeight / 2.0) - frameSize / 2,
    width: frameSize,
    height: frameSize
  }

  // Draw image (Preview canvas)
  dstCtx.drawImage(srcCanvas, DEFAULT_DRAW_MARGIN, DEFAULT_DRAW_MARGIN, dstWidth, dstHeight)

  // Draw image (Temp canvas)
  const minDim = Math.min(srcWidth, srcHeight)
  dstCtxEx.drawImage(srcCanvas,
    srcWidth / 2.0 - minDim / 2.0,
    srcHeight / 2.0 - minDim / 2.0,
    minDim,
    minDim,
    0, 0, imgSize, imgSize)

  // Draw shadow frame (Preview canvas)
  drawFrame(dstCtx, roiRect, frameMargin + DEFAULT_SHADOW_OFFSET,
    frameLength, DEFAULT_DARK_COLOR, DEFAULT_FRAME_WIDTH)

  // Draw frame (Preview canvas)
  drawFrame(dstCtx, roiRect, frameMargin, frameLength,
    DEFAULT_WHITE_COLOR, DEFAULT_FRAME_WIDTH)
}

// Draw frame
function drawFrame (ctx, roiRect, frameMargin, frameLength, frameColor, frameWidth) {
  ctx.beginPath()

  ctx.strokeStyle = frameColor
  ctx.lineWidth = frameWidth

  // top left
  ctx.moveTo(roiRect.x + frameMargin + frameLength, roiRect.y + frameMargin)
  ctx.lineTo(roiRect.x + frameMargin, roiRect.y + frameMargin)
  ctx.lineTo(roiRect.x + frameMargin, roiRect.y + frameMargin + frameLength)

  // bottom left
  ctx.moveTo(roiRect.x + frameMargin, roiRect.y + roiRect.height - frameMargin - frameLength)
  ctx.lineTo(roiRect.x + frameMargin, roiRect.y + roiRect.height - frameMargin)
  ctx.lineTo(roiRect.x + frameMargin + frameLength, roiRect.y + roiRect.height - frameMargin)

  // top right
  ctx.moveTo(roiRect.x + roiRect.width - frameMargin - frameLength, roiRect.y + frameMargin)
  ctx.lineTo(roiRect.x + roiRect.width - frameMargin, roiRect.y + frameMargin)
  ctx.lineTo(roiRect.x + roiRect.width - frameMargin, roiRect.y + frameMargin + frameLength)

  // bottom right
  ctx.moveTo(roiRect.x + roiRect.width - frameMargin, roiRect.y + roiRect.height - frameMargin - frameLength)
  ctx.lineTo(roiRect.x + roiRect.width - frameMargin, roiRect.y + roiRect.height - frameMargin)
  ctx.lineTo(roiRect.x + roiRect.width - frameMargin - frameLength, roiRect.y + roiRect.height - frameMargin)

  ctx.stroke()
  ctx.closePath()
}



