<template>
    <custom-section sec-id="highlights"
                    sec-title="Highlights"
                    :sec-wide="false"
                    :sec-divider="false">

      <!-- Highlights bullet items -->
      <ul class="list-group fa-ul">
        <li v-for="(item,index) in blItems" :key="index" class="pt-3 pb-3">
          <h6 class="font-weight-bold">
            <span class="fa-li"><i class="fas fa-angle-right"></i></span>
            {{item}}
          </h6>
        </li>
      </ul>

      <!-- Carousel label -->
      <p class="pt-3 pb-3">Some of the pictures and the figures of the paper are illustrated below:</p>

      <!-- Carousel -->
      <div id="highlightsCarousel" class="carousel slide rounded border mb-4" data-ride="carousel">
        <!-- Indicators -->
        <ol class="carousel-indicators pt-5">
          <li data-target="#highlightsCarousel" v-for="(item,index) in crItems" :key="index" :data-slide-to="index" :class="{'active':index===0}"></li>
        </ol>

        <!-- Pictures and subtitles -->
        <div class="carousel-inner">
          <div class="carousel-item"  v-for="(item,index) in crItems" :key="index" :class="{'active':index===0}" >
            <img class="d-block w-100" :src="item.url">
            <div class="carousel-caption">

              <h6 class="font-weight-normal p-3 rounded" style="background-color: #000a">{{item.title}}</h6>
            </div>
          </div>
        </div>

        <!-- Buttons -->
        <a class="carousel-control-prev " href="#highlightsCarousel" role="button" data-slide="prev">
          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
          <span class="sr-only">Previous</span>
        </a>
        <a class="carousel-control-next" href="#highlightsCarousel" role="button" data-slide="next">
          <span class="carousel-control-next-icon" aria-hidden="true"></span>
          <span class="sr-only">Next</span>
        </a>
      </div>
    </custom-section>
</template>

<script>
import CustomSection from '../components/elements/CustomSection'
export default {
  name: 'Highlights',
  components: {CustomSection},
  data: function () {
    return {

      blItems: [
        'Gathering an unsupervised image dataset of vehicle models from a marketing site.',
        'Studying the behavior and the performance of six well-known deep architectures in the vehicle recognition case using the transfer learning method on the gathered noisy dataset.',
        'Exploiting the Isolation Forest algorithm with CNN features to discard the external noise of the dataset and analyzing the noise reduction results.',
        'Demonstrating that a high recognition accuracy is attainable with the transfer learning method and it can be elevated using the proposed noise reduction framework.',
        'Providing an android application in which the proposed approach is implemented.'],
      crItems: [
        {
          'url': '/static/media/gallery/general-approach.jpg',
          'title': 'A general overview of the proposed approach'
        },
        {
          'url': '/static/media/gallery/detailed-approach.jpg',
          'title': 'Detailed overview of the proposed approach'
        },
        {
          'url': '/static/media/gallery/primary-results.jpg',
          'title': 'Primary results obtained by training SVM classifiers on the clean and noisy dataset'
        },
        {
          'url': '/static/media/gallery/outlier-benchmark.jpg',
          'title': 'Isolation Forest performance using different feature extractors and different amount of external noise.'
        },
        {
          'url': '/static/media/gallery/final-results.jpg',
          'title': 'The flow of recognition accuracy while applying the noise reduction process'
        },
        {
          'url': '/static/media/gallery/demo-app-01.jpg',
          'title': 'A sample picture of the proposed application.'
        },
        {
          'url': '/static/media/gallery/demo-app-02.jpg',
          'title': 'A sample picture of the proposed application.'
        }

      ]
    }
  }
}
</script>

<style scoped>

</style>



