<template>
  <custom-section sec-id="demo"
                  sec-title="Live Demo"
                  :sec-wide="true"
                  :sec-divider="false">
    <p class="section-body">
      This section provides a minimal applet that demonstrates the classification capability of this project.
      For this aim, the <a class="text-dark" href="https://js.tensorflow.org/" target="_blank"><b>Tensorflow.JS</b></a>
      library is utilized and few lightweight models are used. The
      models were trained on one of the provided divisions of the gathered dataset and have different
      classifiers. The numbers appear in the Top-9 prediction section, are the output values from the
      specified classifier. If the selected network has a Softmax classifier, the values can be interpreted as
      the probabilities of the predicted classes. On the other hand, if the selected network has a Support
      Vector Classifier, the values represent the distance with respect to the SVM decision boundaries.
    </p>

    <div class="container pt-3">
      <!-- Recognition helper elements -->
      <div class="d-none">
        <img :src="imgSrc" ref="imgHandle"/>
        <canvas :width="inputSize" :height="inputSize" ref="cvTemp">
        </canvas>
      </div>

      <!-- Recognition panel -->
      <div class="card mt-2 mb-2 shadow-sm">
        <!-- Rec panel title -->
        <div class="card-header bg-white">Perform Recognition</div>

        <!-- Rec panel body -->
        <div class="card-body">
          <div class="container">
            <div class="row">

              <!-- Choose-input subsection -->
              <div class="col-xl-6 col-12">

                <label>Choose an input image:</label>
                <!-- Samples container -->
                <div class="container p-3 border samples-container rounded mt-1">
                  <div class="row">
                    <sample-item v-for="(item,index) in sampleList"
                                 :spl-index="index"
                                 :spl-item="item"
                                 :key="index"
                                 @splClick="selectSample">
                    </sample-item>
                  </div>
                </div>

                <!-- Upload-image form -->
                <form class="pt-3">
                  <label>Or upload one yourself:</label>
                  <div class="custom-file">
                    <input class="custom-file-input" type="file" id="uploadImg" @change="inputChange"
                           :disabled="isBusy">
                    <label class="custom-file-label" for="uploadImg">Choose file...</label>
                    <div class="invalid-feedback">Invalid input file</div>
                  </div>
                </form>

              </div>

              <!-- Input-preview subsection -->
              <div class="col ada-col  align-self-center ">

                <!-- Preview canvas -->
                <div class="row justify-content-center">
                  <canvas class="preview-canvas rounded" ref="cvPrev" width="340" height="340">
                    Your browser does not support the HTML5 canvas tag.
                  </canvas>
                </div>

                <!-- Control section -->
                <div class="row pt-2 justify-content-center">

                  <!-- Rec button and result label -->
                  <div class="input-group pt-3 pb-3 pl-1 pr-1 w-75">
                    <!-- Button -->
                    <div class="input-group-prepend">
                      <button class="btn btn-success" type="button"
                              @click="recognize"
                              :disabled="isBusy || !imgSrc">
                        Recognize
                      </button>
                    </div>
                    <!-- Labels -->
                    <div class="form-control">
                      <label class="font-weight-normal">{{recLabel}}</label>
                    </div>
                  </div>
                  <!-- Auto-Rec checkbox -->
                  <div class="custom-control custom-checkbox ">
                    <input type="checkbox" class="custom-control-input" id="auto-rec" v-model="autoRec"
                           :disabled="isBusy">
                    <label class="custom-control-label" for="auto-rec">
                      Automatically perform recognition as the input changes
                    </label>
                  </div>

                </div>
              </div>

            </div>
          </div>
        </div>
      </div>

      <!-- Details panel -->
      <div class="card shadow-sm mb-5 mt-5">
        <!-- Details panel title -->
        <div class="card-header bg-white">
          Further Details
        </div>

        <!-- Details panel body -->
        <div class="card-body">
          <div class="container">
            <div class="row">

              <!-- Choose-network subsection -->
              <div class="col-xl-6 col-12">

                <!-- Networks list -->
                <form>
                  <label class="float-left">Select your network:</label>
                  <select class="custom-select " @change="changeNetwork" :disabled="isBusy">
                    <option v-for="(item,index) in networkList" :key="index" :value="index">{{item.name}}
                    </option>
                  </select>
                </form>

                <!-- Networks list -->
                <label class="mt-3">Network details</label>
                <table class="table table-font border">
                  <tbody>
                  <tr v-for="(value,key,index) in currentNet" :key="index">
                    <td class="font-weight-bold w-40">{{key}}</td>
                    <td class="w-60">{{value}}</td>
                  </tr>

                  <!-- Network status row -->
                  <tr class="cell-animate bg-success text-white"
                      :class="{'bg-white':isLoading, 'text-dark':isLoading,'bg-danger':hasError}">
                    <td class="font-weight-bold w-40">Status:</td>
                    <td class="w-60">
                      <span v-if="isLoading">Loading the model...</span>
                      <span v-else-if="!hasError">Successfully loaded.</span>
                      <span v-else>{{errorMsg}}</span>
                      <div class="spinner-grow-sm spinner-grow float-right" role="status" v-show="isLoading">
                        <span class="sr-only">Loading...</span>
                      </div>
                    </td>
                  </tr>

                  </tbody>

                </table>

              </div>

              <!-- Chart-result subsection -->
              <div class="col ada-col-ex align-self-center ">
                <label>Top-9 Predictions:</label>
                <div class=" m-3 hc-size" id="container"></div>
              </div>

            </div>
          </div>

        </div>
      </div>

    </div>
  </custom-section>
</template>

<script>

import SampleItem from '../components/elements/SampleItem'
import TensorNetwork from '../tensor-net'
import {procImg} from '../graphic-utils'
import ChartConfig from '../chart-conf'
import CustomSection from '../components/elements/CustomSection'

let DEFAULT_IMAGE_SIZE = 127

function sleep (ms) {
  return new Promise(resolve => setTimeout(resolve, ms))
}

export default {
  name: 'Demo',
  components: {CustomSection, SampleItem},
  data: function () {
    return {
      // Network vars
      sampleList: [],
      currentNet: null,
      networkList: [],
      recLabel: 'Please choose an image',

      // State flags
      isLoading: false,
      isBusy: false,
      warmUp: false,
      autoRec: true,

      // Exception-handling vars
      hasError: false,
      errorMsg: '',

      // Image-processing vars
      imgSrc: null,
      inputSize: DEFAULT_IMAGE_SIZE
    }
  },
  mounted: function () {
    // Array shuffle function
    Array.prototype.shuffle = function () {
      var input = this

      for (var i = input.length - 1; i >= 0; i--) {
        var randomIndex = Math.floor(Math.random() * (i + 1))
        var itemAtIndex = input[randomIndex]

        input[randomIndex] = input[i]
        input[i] = itemAtIndex
      }
      return input
    }
    // --

    let vm = this

    // Init File-Reader
    vm.fileReader = new FileReader()
    vm.fileReader.onload = (event) => {
      vm.imgSrc = event.target.result
    }

    // Init Image-Onload event
    let imgHandle = this.$refs.imgHandle

    imgHandle.onload = async () => {
      procImg(imgHandle, vm.$refs.cvPrev, vm.$refs.cvTemp, vm.inputSize)
      if (vm.autoRec) {
        vm.recognize()
      }
    }
    // Init Highcharts
    // eslint-disable-next-line
      vm.chart = Highcharts.chart('container', ChartConfig)

    // Fetch samples list
    vm.fetchJson('/static/content/json/samples.json').then((jsonObj) => {
      vm.sampleList = jsonObj.shuffle()
    })

    // Fetch models list
    vm.fetchJson('/static/models/networks.json').then((jsonObj) => {
      vm.networkList = jsonObj
      vm.loadNetwork()
    })
  },
  methods: {
    loadNetwork: async function (index = 0) {
      let vm = this

      try {
        // Update state flags
        vm.isLoading = true
        vm.isBusy = true

        // Assign current network
        vm.currentNet = vm.networkList[index].details

        // Dispose the previous loaded network
        if (vm.tensorModel != null) {
          vm.tensorModel.disposeModel()
        }
        // Initialize new tensor-net class
        vm.tensorModel = new TensorNetwork()
        vm.inputSize = vm.networkList[index].inputSize

        // Load the specified network
        await vm.tensorModel.initModel(vm.networkList[index])
        await sleep(1000)

        // Successfully loaded, update flags
        vm.hasError = false
        vm.errorMsg = ''
        vm.warmUp = false

        // Preprocess current image if there exists any
        if (vm.imgSrc != null) {
          let imgHandle = this.$refs.imgHandle
          procImg(imgHandle, vm.$refs.cvPrev, vm.$refs.cvTemp, vm.inputSize)
        }

        // Successfully done ,Update state flags
        vm.isLoading = false
        vm.isBusy = false
        vm.hasError = false
        vm.errorMsg = ''
        vm.warmUp = false
      } catch (e) {
        // Update state flags
        vm.isLoading = false
        vm.isBusy = false

        vm.hasError = true
        vm.errorMsg = e.message
      }
    },
    recognize: async function () {
      let vm = this

      try {
        // Update state flags
        vm.isBusy = true

        // Show warp-up notification
        if (!vm.warmUp) {
          // eslint-disable-next-line
            alertify.notify('Please wait while the network is being loaded...')
          await sleep(1000)
        }

        // Perform recognition
        let result = await this.tensorModel.recognize(this.$refs.cvTemp)

        // Create Highchart compatible result array
        const resArray = []
        for (let i = 0; i < result.length; i++) {
          resArray.push([result[i].className, result[i].probability])
        }

        // Update the results
        vm.recLabel = result[0].className
        vm.chart.series[0].setData(resArray)

        // Update warm-up state
        if (!this.warmUp) {
          // eslint-disable-next-line
            alertify.notify('Successfully loaded.')
          vm.warmUp = true
        }

        // Update state flags
        vm.isBusy = false
      } catch (exception) {
        // eslint-disable-next-line
          alertify.error(exception.message)
        // Update state flags
        vm.isBusy = false
      }
    },
    changeNetwork: function (index) {
      this.loadNetwork(index.target.value)
    },
    inputChange: function (event) {
      let files = event.target.files
      this.fileReader.readAsDataURL(files[0])
    },
    selectSample: function (index) {
      if (!this.isBusy) {
        this.imgSrc = this.sampleList[index].url
      }
    },
    fetchJson: async function (jsonUrl) {
      try {
        let promise = await fetch(jsonUrl)
        return await promise.json()
      } catch (e) {
        console.error('Error occurred while loading the json file' + jsonUrl)
        console.error(e.message)
        return null
      }
    }

  }
}
</script>

<style scoped>

  .section-body {
    text-align: justify;
    line-height: 1.6;
  }

  .samples-container {
    height: 22rem;
    -webkit-overflow-y: scroll;
    -moz-overflow-y: scroll;
    -ms-overflow-y: scroll;
    -o-overflow-y: scroll;
    -khtml-overflow-y: scroll;
    overflow-y: scroll;
  }

  @media (min-width: 1200px) {
    .ada-col {
      border-left: 1px solid #dee2e6 !important;
      margin-left: 2rem !important;

    }
  }

  @media (max-width: 1200px) {
    .ada-col {
      border-top: 1px solid #dee2e6 !important;
      margin: 3rem !important;
      padding-top: 3rem !important;
    }
  }

  .preview-canvas {

    border: 1px solid #d3d3d3;
    background: url('/static/media/no-image.png') no-repeat center;
    background-size: 4rem;
  }

  @media (min-width: 1200px) {
    .ada-col-ex {
      border-left: 1px solid #dee2e6 !important;
      margin-left: 1rem !important;

    }
  }

  @media (max-width: 1200px) {
    .ada-col-ex {
      border-top: 1px solid #dee2e6 !important;
      margin: 3rem !important;
      padding-top: 3rem !important;
    }
  }

  @media (min-width: 1200px) {
    .hc-size {
      width: 26rem;
      height: 22rem;
    }
  }

  @media (max-width: 1200px) {
    .hc-size {
      width: auto;
      height: auto;
    }
  }

  .table-font {
    font-size: 0.8rem;
  }

  .cell-animate {
    -webkit-transition: 0.3s ease-in-out;
    -moz-transition: 0.3s ease-in-out;
    -ms-transition: 0.3s ease-in-out;
    -o-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
  }

   .w-40{
    width: 30%;
  }

   .w-60{
    width:70%;
  }
</style>



