<template>
  <nav class="site-header navbar navbar-expand-lg navbar-dark" ref="navHandle" id="navbar-menu">

    <div class="container d-flex flex-md-row width-fix">

      <!-- NavBar logo-->
      <img v-if="logoSrc" class="logo-img" :src="logoSrc"/>

      <!-- NavBar toggle button-->
      <button class="navbar-toggler"  type="button" data-toggle="collapse" data-target="#navItems"
              aria-controls="navItems" aria-expanded="false" aria-label="Toggle">
        <span class="navbar-toggler-icon"></span>
      </button>

      <!-- NavBar items -->
      <div class="navbar-collapse collapse" id="navItems" >
        <ul class="navbar-nav nav-pills">
          <li class="nav-item" v-for="(item,index) in navItems" :key="index">
            <a class="nav-link pt-2" :class="{active:index===0}" :href="item.href">{{item.name}}</a>
          </li>
        </ul>
      </div>

    </div>
  </nav>

</template>

<script>
export default {
  name: 'TheNavbar',
  data: function () {
    return {
      logoSrc: '/static/media/header-logo.svg',
      navItems: [

        {name: 'About', href: '#about'},
        {name: 'Abstract', href: '#abstract'},
        {name: 'Highlights', href: '#highlights'},
        {name: 'Live Demo', href: '#demo'},
        {name: 'Publication', href: '#publication'},
        {name: 'Database', href: '#dataset'},
        {name: 'Codes', href: '#codes'}
      ]
    }
  },
  mounted: function () {
    let vm = this
    window.onscroll = () => {
      let scrollValue = window.scrollY

      if (scrollValue > 300) {
        vm.$refs.navHandle.classList.add('sticky-top')
      } else {
        vm.$refs.navHandle.classList.remove('sticky-top')
      }
    }
  }
}
</script>

<style scoped>

  .site-header {
    background-color: rgba(0, 0, 0, .8);
    -webkit-backdrop-filter: saturate(180%) blur(10px);
    backdrop-filter: saturate(180%) blur(10px);
  }

  .width-fix {
    width: fit-content !important;
  }

  .logo-img {
    width: 3rem;
    height: 3rem;
    margin-right: 3rem;
  }

  .nav-pills .nav-link:hover {
    border-bottom: .2rem solid white;
    border-radius: 0;
  }

  .nav-pills .nav-link {
    border-bottom: .2rem solid transparent;
    border-radius: 0;
  }

  .nav-pills .nav-link.active {
    background-color: transparent !important;
    border-bottom: .2rem solid white;
  }

  .navbar-collapse ul {
    list-style-type: none;
    padding: 0;
  }

  .navbar-collapse li {
    display: inline-block;
    margin: 0 0.75rem;
  }

</style>



