<template>
  <div id="app">

    <!-- Jumbotron -->
    <the-jumbotron class="text-center"></the-jumbotron>
    <!-- Navbar -->
    <the-navbar></the-navbar>

    <!-- Main -->
    <main>
      <!-- Upper section -->
      <section class="bg-white">
        <div class="container pt-5 pb-5" style="margin-top:7rem;">
          <!-- About -->
          <about></about>
          <!-- Abstract -->
          <abstract></abstract>
          <!-- Highlights -->
          <highlights></highlights>
        </div>
      </section>

      <!-- Demo section -->
      <section class="bg-light border-top border-bottom">
        <div class="container pt-5">
          <demo></demo>
        </div>
      </section>

      <!-- Lower section -->
      <section class="bg-white">
        <div class="container pt-5 pb-5">
          <!-- Publication details -->
          <publication></publication>
          <!-- Dataset -->
          <dataset></dataset>
          <!-- Codes -->
          <codes></codes>
        </div>
      </section>
    </main>

    <!-- The footer -->
    <the-footer></the-footer>
  </div>
</template>

<script>

import TheJumbotron from './components/layout/TheJumbotron'
import TheFooter from './components/layout/TheFooter'
import TheNavbar from './components/layout/TheNavbar'
import Highlights from './sections/Highlights'
import Dataset from './sections/Dataset'
import Publication from './sections/Publication'
import Codes from './sections/Codes'
import About from './sections/About'
import Abstract from './sections/Abstract'
import Demo from './sections/Demo'

export default {
  name: 'App',
  components: {
    Demo,
    Abstract,
    About,
    Codes,
    Publication,
    Dataset,
    Highlights,
    TheNavbar,
    TheFooter,
    TheJumbotron
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }
</style>



