import * as tf from '@tensorflow/tfjs'

export default class TensorNetwork {
  // Class Variables
  tfModel;
  modelUrl;
  weightsManifestUrl;
  labelsUrl;
  inputSize;
  inputDim;
  inputMean;
  threshold;
  classes;
  batchDim;
  labelsArray;

  constructor () {
    this.tfModel = null
    // Assign default values
    this.modelUrl = ''
    this.weightsManifestUrl = ''
    this.labelsUrl = ''
    this.inputSize = 227
    this.inputDim = 3
    this.inputMean = [123.0, 117.0, 104.0]
    this.threshold = -0.2
    this.classes = 9
    // --
    this.batchDim = null
    this.labelsArray = null
  }

  async initModel (netPrototype = null) {
    if (netPrototype != null) {
      // Get network parameters
      this.modelUrl = netPrototype.modelUrl
      this.weightsManifestUrl = netPrototype.weightsManifestUrl
      this.labelsUrl = netPrototype.labelsUrl
      this.inputSize = netPrototype.inputSize
      this.inputDim = netPrototype.inputDim
      this.inputMean = netPrototype.inputMean
      this.threshold = netPrototype.threshold
      this.classes = netPrototype.classes
      console.log('init parameters')
      console.log(this)
    }

    // Determine batch dimensions
    this.batchDim = [1, this.inputSize, this.inputSize, this.inputDim]

    // Fetch labels
    if (this.labelsUrl != null) {
      this.labelsArray = await this.fetchJson(this.labelsUrl)
      console.log('labels loaded')
      console.log(this.labelsArray)
    }

    // Load TensorFlow frozen model
    this.tfModel = await tf.loadFrozenModel(this.modelUrl, this.weightsManifestUrl)
    // Warm up the model
    // let dummyData = tf.zeros(this.batchDim)
    // this.tfModel.predict(dummyData).dispose()
  }

  /* Taken from Tenforflow.js examples */
  async getTopKClasses (rawOutput, topK, labels) {
    const values = await rawOutput.data()

    const valuesAndIndices = []
    for (let i = 0; i < values.length; i++) {
      valuesAndIndices.push({value: values[i], index: i})
    }
    valuesAndIndices.sort((a, b) => {
      return b.value - a.value
    })
    const topkValues = new Float32Array(topK)
    const topkIndices = new Int32Array(topK)
    for (let i = 0; i < topK; i++) {
      topkValues[i] = valuesAndIndices[i].value
      topkIndices[i] = valuesAndIndices[i].index
    }

    const topClassesAndProbs = []
    for (let i = 0; i < topkIndices.length; i++) {
      topClassesAndProbs.push({
        className: labels[topkIndices[i]],
        probability: topkValues[i]
      })
    }
    return topClassesAndProbs
  }

  async recognize (cvElement) {
    const rawOutput = tf.tidy(() => {
      // Get a Tensor object from canvas element
      const imgTensor = tf.fromPixels(cvElement).toFloat()

      // Create mean Tensor
      const imgMean = tf.tensor(this.inputMean)

      // Subtract the mean, the pixel range should be between [0,255], hence, no normalization is needed
      const imgNorm = imgTensor.sub(imgMean)

      // Create input batch
      const imgBatch = imgNorm.reshape(this.batchDim)

      // Perform the prediction
      return this.tfModel.predict(imgBatch)
    })

    // Convert logits to probabilities and class names.
    return this.getTopKClasses(rawOutput, this.classes, this.labelsArray)
  }

  async fetchJson (jsonUrl) {
    try {
      let promise = await fetch(jsonUrl)
      return await promise.json()
    } catch (e) {
      console.error('Error occurred while loading the json file' + jsonUrl)
      console.error(e.message)
      return null
    }
  }

  disposeModel () {
    if (this.tfModel != null) {
      this.tfModel.dispose()
      return true
    }
    return false
  }
}



