<template>
  <div class="row justify-content-center" :id="secId">
    <div v-bind:class="secWide ? wideClass:normClass">
      <!-- Section title -->
      <h4 v-if="secTitle"  class="pb-3 font-weight-bold">{{secTitle}}</h4>
      <!-- Section body -->
      <slot></slot>
      <!-- Section divider -->
      <hr v-if="secDivider" class="mt-5 mb-5">
    </div>
  </div>
</template>

<script>
export default {
  name: 'CustomSection',
  props: ['secId', 'secTitle', 'secWide', 'secDivider'],
  data: function () {
    return {
      wideClass: ['col-11', 'col-md-12'],
      normClass: ['col-lg-7', 'col-10']
    }
  }
}
</script>

<style scoped>

</style>



