<template>

  <footer class="bg-light border-top text-center">
    <div class="container p-3">
      <div class="row justify-content-center">

        <!-- Footer logo -->
        <div v-if="ftImgUrl" class="col-1">
          <img class="footer-img" :src="ftImgUrl"/>
        </div>

        <!-- Footer details -->
        <div v-if="ftTitle || ftSubtitle" class="col-9">
          <h6>{{ftTitle}}</h6>
          <small class="text-muted" v-html="ftSubtitle"></small>
        </div>

      </div>
    </div>
  </footer>

</template>

<script>
export default {
  name: 'TheFooter',
  data: function () {
    return {
      ftImgUrl: '/static/media/footer-logo.png',
      ftTitle: 'Transfer Learning Approach for Classification and Noise Reduction on Noisy Web Data',
      ftSubtitle: 'Developed and Maintained By Sina M.Baharlou<br>Updated on December 2018'
    }
  }
}
</script>

<style scoped>
.footer-img{
  width:4rem;
  height:4rem;
}
</style>



