<template>
  <custom-section sec-id="codes"
                  sec-title="Codes"
                  :sec-wide="false"
                  :sec-divider="false">
    <p class="section-body">
      This project also provides an Android demonstration application, in which a few lightweight models are
      implemented. The Tensorflow and OpenCV libraries are used to developed this application, and it has been tested on
      the Lollipop version of Android. You can find the source and the pre-built binary file in the following Github
      repository.</p>
    <!-- Goto button-->
      <a href="" target="_blank" class="btn btn-outline-dark">
        <em class="fab fa-github btn-icon"></em>
        Go to Github
      </a>
  </custom-section>
</template>

<script>
import CustomSection from '../components/elements/CustomSection'

export default {
  name: 'Codes',
  components: {CustomSection}
}
</script>

<style scoped>
  .section-body {
    text-align: justify;
    line-height: 1.6;
  }

  em.btn-icon {
    margin-right: 0.25rem !important;
    line-height: 1rem;
  }
</style>



