<template>
  <custom-section sec-id="about"
                  sec-title="What is Vehicle Recognition Project About?"
                  :sec-wide="false"
                  :sec-divider="true">
    <p class="section-body">
      Vehicle Recognition project, as the name suggests, is a system designed to distinguish
      between different car models. This work is a scientific experiment, which unlike the other recognition systems, is
      using a different approach to learn the specified task. The proposed method is thoroughly explained in the related
      publication called <a class="text-dark"
                            href="https://www.sciencedirect.com/science/article/pii/S0957417418301878?via%3Dihub"
                            target="_blank"><b>"Transfer Learning Approach for Classification and Noise Reduction on
      Noisy Web Data"</b></a>.
      In this paper, the recognition goal is achieved by training several renowned Convolutional Neural Networks on an
      unsupervised dataset gathered from web sources. The transfer learning strategy is employed and the gathered
      dataset is weakly annotated and contains a significant amount of noisy samples. In addition, a novel approach of
      discarding the external noise is presented.
      In this page, you can find the details of the publication, and various supplementary materials used in this work,
      including the dataset, the android application with the source code, and a live demo of the work.
    </p>
  </custom-section>
</template>

<script>
import CustomSection from '../components/elements/CustomSection'

export default {
  name: 'About',
  components: {CustomSection}
}
</script>

<style scoped>
  .section-body {
    text-align: justify;
    line-height: 1.6;
  }
</style>



