const ChartConfig = {
  chart: {
    type: 'column'
  },
  title: {
    text: ''
  },
  subtitle: {
    text: ''
  },
  xAxis: {
    type: 'category',
    labels: {
      rotation: -45,
      style: {
        fontSize: '13px',
        fontFamily: 'Verdana, sans-serif'
      }
    }
  },
  yAxis: {

    title: {
      text: 'Accuracy (Distance from SVM decision boundaries)'
    }
  },
  legend: {
    enabled: false
  },
  tooltip: {
    pointFormat: 'Accuracy: <b>{point.y:.1f} %</b>'
  },
  series: [{
    name: 'Accuracy',
    data: [
      ['Xantia', 0.0],
      ['Peugeot Persia', 0.0],
      ['Peykan', 0.0],
      ['Samand', 0.0],
      ['Peugeot 206', 0.0],
      ['Peugeot 405', 0.0],
      ['Logan', 0.0],
      ['Pride', 0.0],
      ['Tiba', 0.0]
    ],

    color: 'rgba(50,200,70,0.6)',
    borderColor: 'gray',
    dataLabels: {
      enabled: true,
      rotation: -90,
      color: '#FFFFFF',
      align: 'right',
      format: '{point.y:.1f}', // one decimal
      y: 10, // 10 pixels down from the top
      style: {
        fontSize: '13px',
        fontFamily: 'Verdana, sans-serif'
      }
    }
  }]
}

export default ChartConfig



