<template>
   <!-- Sample pack (column) -->
    <div v-if="splItem" class="col-6 col-md-3 col-sample" @click="$emit('splClick',splIndex)">

      <!-- Sample image -->
      <div class="row justify-content-center">
        <img class="shadow img-sample"
             data-toggle="tooltip"
             data-placement="top"
             title="Click to perform recognition"
             :src="splItem.thumb">
      </div>

      <!-- Sample label -->
      <div class="row justify-content-center">
        <small v-if="splItem.label" class="muted badge badge-secondary m-2 shadow-sm">{{splItem.label}}</small>
        <small v-else class="muted badge badge-secondary m-2">Not defined</small>
      </div>

    </div>
</template>

<script>
export default {
  name: 'SampleItem',
  props: ['splItem', 'splIndex']
}
</script>

<style scoped>

  .col-sample{
    cursor: pointer;

  }

  .col-sample .img-sample{
    height: 4rem;
    width: 4rem;
    margin: 1rem 0;

    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;

    -webkit-transition: 0.15s ease-in-out;
    -moz-transition: 0.15s ease-in-out;
    -ms-transition: 0.15s ease-in-out;
    -o-transition: 0.15s ease-in-out;
    transition: 0.15s ease-in-out;
  }

  .col-sample:hover .img-sample{
    height: 6rem;
    width: 6rem;
    margin: 0 0;
  }

  .col-sample:hover .badge{
    -webkit-transition: 0.15s ease-in-out;
    -moz-transition: 0.15s ease-in-out;
    -ms-transition: 0.15s ease-in-out;
    -o-transition: 0.15s ease-in-out;
    transition: 0.15s ease-in-out;
    background-color: black;

  }
</style>



